import React, { Component } from 'react';
import { Popover, PopoverBody } from 'reactstrap';
import miniDashLogo from '../../../../images/icons/dashboard.png';
import { withStyles } from '@material-ui/core/styles';
import styles from './Styles';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import MenuItems from '../ContextualizeMenu/MenuItems';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import APITransport from '../../../actions/apitransport/apitransport';
import { Card, CardBody, CardTitle } from 'reactstrap';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';

class MiniDashboard extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            popoverOpen: false
        };

    }

    toggle() {
        this.setState({
            popoverOpen: !this.state.popoverOpen
        });
    }

    handleChange(route) {
        if (this.props.location.pathname !== route) {
            this.props.history.push(route)
        }
    };

    getData(level) {
        let kpi = (this.props.plvfilter.KPI && this.props.plvfilter.KPI.length) ? this.props.plvfilter.KPI : this.props.kpi;
        let data = kpi.filter(ele => { return ele.name === level });
        if (!data) {
            return { "name": level, "color": "green", "count": 0 }
        }
        return data[0];
    }

    getAlertData(level) {
        let alert = this.props.alerts;
        let data = alert.filter(ele => { return ele.name === level });
        if (!data) {
            return { "name": level, "color": "green", "count": 0 }
        }
        return data[0];
    }

    getTaskCount(level) {
        let task = this.props.tasks;
        let data = task.filter(ele => { return ele.name === level });
        if (!data) {
            return { "name": level, "color": "green", "count": 0 }
        }
        return data[0];
    }

    prepareEventItem() {
        let classes = this.props.classes;
        return (
            this.props.events.map((item, index) => {
                return [
                    <List key={index} >
                        {
                            <ListItem className={classes.itemList}>
                                <ListItemText className={classes.eventText}
                                    primary={item.title}
                                />
                                <ListItemSecondaryAction className={classes.eventValue}>
                                    {item.value}
                                </ListItemSecondaryAction>
                            </ListItem>

                        }
                    </List>,
                    <Divider key={'div' + index} light />
                ]
            })
        );
    }

    render() {
        let { classes } = this.props;
        return (
            <div>
                <div className={classes.miniDashIcon} id="Popover1" onClick={this.toggle}>
                    <img src={miniDashLogo} alt="COGNIZ" />
                </div>
                <Popover placement="bottom-end"
                    isOpen={this.state.popoverOpen}
                    target="Popover1" toggle={this.toggle}
                    className={classes.popover}
                >

                    <PopoverBody className={classes.popoverBody}>
                        <Card className={classes.card}>
                            <CardBody>
                                <CardTitle><Link to="/kpi/critical" className={classes.anchorLink}> KPI </Link></CardTitle>

                                <List>
                                    <MenuItems title="CRITICAL" count={this.getData('CRITICAL').count} status="critical" icon={true} />
                                    <Divider />
                                    <MenuItems title="MAJOR" count={this.getData('MAJOR').count} status="major" icon={true} />
                                    <Divider />
                                    <MenuItems title="MINOR" count={this.getData('MINOR').count} status="minor" icon={true} />
                                    <Divider />
                                    <MenuItems title="HEALTHY" count={this.getData('HEALTHY').count} status="success" icon={true} />
                                </List>

                            </CardBody>
                        </Card>
                        <Card className={classes.card}>
                            <CardBody>
                                <CardTitle><Link to="/events" className={classes.anchorLink}> EVENTS </Link></CardTitle>
                                {this.prepareEventItem()}
                            </CardBody>
                        </Card>
                        <Card className={classes.card}>
                            <CardBody>
                                <CardTitle><Link to="/alert" className={classes.anchorLink}> ALERT </Link></CardTitle>

                                <List>
                                    <MenuItems title="CRITICAL" count={this.getAlertData('CRITICAL').count} status="critical" icon={true} />
                                    <Divider />
                                    <MenuItems title="MAJOR" count={this.getAlertData('MAJOR').count} status="major" icon={true} />
                                    <Divider />
                                    <MenuItems title="MINOR" count={this.getAlertData('MINOR').count} status="minor" icon={true} />
                                </List>

                            </CardBody>
                        </Card>
                        <Card className={classes.card}>
                            <CardBody>
                                <CardTitle><Link to="/tasks/ALL" className={classes.anchorLink}> Task </Link></CardTitle>
                                <List>
                                    <MenuItems title="FAILED" count={this.getTaskCount('FAILED').count} status="critical" icon={true} />
                                    <Divider />
                                    <MenuItems title="EXECUTED" count={this.getTaskCount('EXECUTED').count} status="success" icon={true} />
                                </List>
                            </CardBody>
                        </Card>

                    </PopoverBody>

                </Popover>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        // kpi: state.dashboard.KPI,
        // counter: state.dashboard,
        // events: state.dashboard.EVENTS,
        // tasks: state.dashboard.TASKS,
        // plvfilter: state.plvfilter,
        // alerts: state.dashboard.ALERTS,
        apistatus: state.apistatus
    }
}
const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        APITransport: APITransport
    }, dispatch)
}


export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(MiniDashboard));


